<template>
	<div  v-loading.body="loading1"  element-loading-text="拼命加载中">
		<el-row style="font-size:14px;">
		  <el-col :span="4">
				{{customerData.name}}
			</el-col>
			<el-col :span="8">
				{{customerData.company}}
			</el-col>
		  <el-col :span="4">
				完善率&nbsp;&nbsp;<i style="color:#409EFF;">{{customerData.perfection_degree}}%</i>
			</el-col>
			<el-col :span="8" style="text-align:right;">
				<a href="javascript:void(0);" @click="changeVersionView" style="color:#409EFF;">查看不同版本</a>
			</el-col>
		</el-row>
		<br>
		<br>
		<el-row style="font-size:14px;">
		  <el-col :span="4">
				版本ID&nbsp;&nbsp;<i style="color:#409EFF;">{{customerData.id}}</i>
			</el-col>
			<el-col :span="8">
				创建时间&nbsp;&nbsp;<i style="color:#409EFF;">{{customerData.create_time}}</i>
			</el-col>
		  <el-col :span="4">
				创建人&nbsp;&nbsp;<i style="color:#409EFF;">{{customerData.admin_name}}</i>
			</el-col>
		</el-row>

		<el-tabs v-model="activelab" type="card" @tab-click="handleClick" style="margin-top:20px;">
			<el-tab-pane label="全部" data-val="" name="all"></el-tab-pane>
 		 	<el-tab-pane label="用户基础信息" name="basic-info" data-val="1"></el-tab-pane>
 		 	<el-tab-pane label="用户特性信息" name="user-feature" data-val="3"></el-tab-pane>
 		 	<el-tab-pane label="会议需求分析" name="recommends" data-val="2"></el-tab-pane>
 		 	<el-tab-pane label="客户公司关系" name="requirements" data-val="4"></el-tab-pane>
  	</el-tabs>

		<el-table
		 :data="customerData.field_info"
		 style="width: 100%"

		 >
		 <el-table-column
			 prop="table"
			 label="字段分类"
			 align="center"
			 :show-overflow-tooltip=true
			>
			</el-table-column>
		 <el-table-column
			 prop="field_name"
			 label="字段名称"
			 :show-overflow-tooltip=true
			 align="center"
			>
		 </el-table-column>


		 <!-- <el-table-column
			 prop="name"
			 label="有该字段的客户数">
		 </el-table-column>
		 <el-table-column
			 prop="amount3"
			 label="已创建客户数"
			 sortable>
		 </el-table-column> -->
		 <el-table-column
			 prop="perfection_degree"
			 label="完善度"
			 align="center"
			 sortable>
		 </el-table-column>


		 <el-table-column
			prop="field_perfection_degree"
			label="平台平均完善率"
			header-align="left"
			:show-overflow-tooltip=true
			align="center"
			sortable
			:formatter="formatter">
		</el-table-column>

		 <el-table-column
			 prop="diff_count"
			 label="不同版本数"
			 header-align="left"
			 align="center"
			 sortable>
		 </el-table-column>
		 <el-table-column
			 prop="agree_count"
			 label="同意次数"
			 align="center"
			 sortable>
		 </el-table-column>
		 <el-table-column
			 prop="field_count"
			 label="创建版本总数"
			 header-align="left"
			 align="center"
			 sortable>
		 </el-table-column>
	 </el-table>
	</div>
</template>
<script>
export default {
	name: 'mngdetail',
	data: function () {
		return {
			loading1:false,
			activelab: 'all',
			customerData: [],
			listParam:{
				type: 0
			}
		}
	},
	watch:{
		'$route' (to, from) {
			var self = this;
			self.activelab = 'all',
			self.customerData = [],
			self.listParam = {
				type: 0
			}
			self.listParam['customer_id'] = self.$route.params['id'];
			self.getList();
		}

	},
	mounted () {
		let self = this;
		// 判断页面来源，根据来源请求参数不同
		self.listParam['customer_id'] = self.$route.params['id'];
		self.getList();
	},
	methods: {
		handleClick(tab, event) {
      var tabName = tab.name;
      var self = this;
			self.listParam['type'] = tab.$attrs['data-val'];
			self.loading1 = true;
			self.getList();
    },
		getList() {
			var self = this;
			self.$ajax.get(self.$api.getModuleHost('CRM', "/manage/getSingleCustomerFieldInfo"),self.listParam).then(function (data) {
				self.customerData = data.data;
				self.loading1 = false;
			}).catch(function (err) {
					console.error(err);
			});
		},
		changeVersionView() {
			var self = this;
			self.$emit('save-data',[self.customerData.name,self.customerData.company,self.customerData.id]);
			self.$parent.currentView = 'mngversion';
		},
		formatter(row, column) {
			 return row.field_perfection_degree+"%";
		 },


	},
}
</script>
<style lang="less">

</style>



