<template>
	<div v-loading.body="loading2"  element-loading-text="拼命加载中">
		<el-row style="font-size:14px;">
		  <el-col :span="12">
				查看不同版本
			</el-col>
			<el-col :span="12" style="text-align:right;">
				<a href="javascript:void(0);" @click="changeVersionView" style="color:#409EFF;">关闭</a>
			</el-col>
		</el-row>
		<br>
		<div style="width:100%;border-top:1px solid #C0CCDA;height:1px;"></div>
		<br>
		<el-row style="font-size:14px;">
		  <el-col :span="4">
				{{customer}}
			</el-col>
			<el-col :span="8">
				{{company}}
			</el-col>
		  <el-col :span="5">
				历史总版本数：&nbsp;&nbsp;<i style="color:#409EFF;font-size:18px;">{{version_count}}</i>
			</el-col>
		</el-row>
		<br>

		<el-table
    :data="versions"
    style="width: 100%">
    <el-table-column type="expand">
      <template slot-scope="props">
				<el-table
				:border=false
		    :data="props.row.field_log"
				style="width: 100%"
		    >
				<el-table-column
					label="字段分类"
					:show-overflow-tooltip=true
					prop="table">
				</el-table-column>
				<el-table-column
					label="字段名称"
					prop="field">
				</el-table-column>
				<el-table-column
					label="操作行为"
					prop="type">
				</el-table-column>
				<el-table-column
					label="操作前信息"
					prop="before_value">
				</el-table-column>
				<el-table-column
					label="操作后信息"
					prop="new_value">
				</el-table-column>
				<el-table-column
					label="是否被同意过"
					prop="is_agree">
				</el-table-column>
				<el-table-column
					label="同意次数"
					prop="agree_times">
				</el-table-column>
				</el-table>
      </template>
    </el-table-column>
    <el-table-column
      label="版本ID"
      prop="version">
    </el-table-column>
    <el-table-column
      label="创建时间"
			sortable
      prop="create_time">
    </el-table-column>
    <el-table-column
      label="创建人"
      prop="admin_name">
    </el-table-column>
		<el-table-column
      label="完善率"
			:formatter="formatter"
			sortable
      prop="perfection_degree">
    </el-table-column>
		<el-table-column
      label="创建字段数"
			sortable
      prop="create_count">
    </el-table-column>
		<el-table-column
      label="修改/删除字段数"
			sortable
      prop="diff_count">
    </el-table-column>
		<el-table-column
      label="同意字段数"
			sortable
      prop="agree_count">
    </el-table-column>
  </el-table>



	</div>
</template>
<script>
export default {


	name: 'mngversion',
	props:[
		'customer',
		'company',
		'c_id'
	],
	watch:{
		'$route' (to, from) {
			console.log(to);
			console.log(from);
		}

	},
	data: function () {
		var self = this;
		return {
			loading2: true,
			versions: [],
			version_count: 0,
			vesionParam:{
				customer_id: self.$route.params.id
			}
		}
	},
	mounted () {
		var self = this;
		// 判断页面来源，根据来源请求参数不同
		self.getVersions();
	},
	methods: {
		getVersions() {
			var self = this;
			self.$ajax.get(self.$api.getModuleHost('CRM', "/manage/getDiffVersion"),self.vesionParam).then(function (data) {
				self.versions = data.data;
				self.version_count = data.version_count;
				self.loading2 = false;

			}).catch(function (err) {
					console.error(err);
			});
		},
		changeVersionView() {
			var self = this;
			self.$parent.currentView = 'mnglatest';
		},
		formatter(row, column) {
			 return row.perfection_degree+"%";
		 },

	},
}
</script>
<style lang="less">
.el-table__expanded-cell{
	padding:20px 10px;
}
</style>



