<template>
	<div >

			<component v-bind:is="currentView" @save-data="saveChildData" :customer="childData[0]" :company="childData[1]" c_id="childData[2]"></component>

	</div>
</template>
<script>
import mnglatest from './mnglatest.vue'
import mngversion from './mngversion.vue'
export default {
	name: 'mngdetail',
	data: function () {
		return {
			currentView: mnglatest,
			childData:[]
		}
	},
	watch:{
		'$route' (to, from) {
			this.currentView = mnglatest;
		}

	},
	methods:{
		saveChildData(data) {
			var self = this;
			self.childData = data;
		}
	},
	components:{
		mnglatest: mnglatest,
		mngversion: mngversion
	}

}
</script>
<style lang="less">

</style>



